.App {
  padding: 0 30px;
}

.Header nav {
  display: flex;
  justify-content: flex-end;
  max-width: 1040px;
  margin: 0 auto;
}

.Header nav a {
  color: black;
  cursor: pointer;
  font-size: 20pr;
  font-weight: bold;
  display: block;
  margin: 10px;
  text-decoration: underline;
}

.SignupForm {
  display: flex;
  flex-direction: column;
}

.SignupForm input,
.SignupForm button {
  margin-bottom: 30px;
  max-width: 400px;
  width: 100%;
}

.box {
  font-size: 15pt;
}
